.TennisPlayer {
    /* display: flex;
    justify-content: center;
    align-items: center; */
    /* width: 100%; */
    height: 100%;
    background-color: aqua;
    min-height: calc(100vh - 124.5px);

    padding-top: 100px;
    padding-bottom: 20px;
    box-sizing: border-box;
    /* margin-top: -74Px; */
    background-image: url(../../../Assets//Images/grass-court.jpeg);
    background-repeat: no-repeat;
    background-size: cover;

}

.TennisPlayerContainer {
    /* margin-top: 40px; */
    /* background-color: rgb(107, 149, 185); */
    display: grid;
    grid-template-columns: 60% 40%;
    width: 70%;
    border-radius: 10px;
    border: 2px solid black;
    margin: auto;
    color: whitesmoke;
    font-weight: bold;
     background-image: url(../../../Assets//Images/dark-tennis4.jpeg);
     
    background-repeat: no-repeat;
    background-size: cover;
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */



}


.TennisPlayer .PlayerImageDiv {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-image: url(../../../Assets//Images/photo-1558365849-6ebd8b0454b2.jpeg);
    background-repeat: no-repeat;
    background-size: cover; */
    margin: 0;
    padding: 0;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    /* background-color: rgba(13, 128, 228, 0.099); */




}

.TennisPlayer .FlagImage {
    position: absolute;
    top: 20px;
    right: 25px;
    border-radius: 90px;
    width: 100px;
    height: 100px;
}

.TennisPlayer .PlayerImage {
    max-width: 300px;
}
.TennisPlayer .PlayerInfo {
    /* padding: 10px; */
    display: grid;
    grid-template-columns: 50% 50%;
    /* border-top-left-radius: 8px; */
    border-bottom-left-radius: 8px;
    /* background-image: url(../../../Assets//Images/dark-tennis4.jpeg);
    background-repeat: no-repeat;
    background-size: cover; */
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    background-color: rgba(13, 128, 228, 0.099);





}

.PlayerInfo h1 {
    grid-column: span 2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem


}

.PlayerInfo> div {
    border: 1px solid black;
    /* padding: 10px; */
    /* display: flex;
    justify-content: flex-start;
    align-items: center; */

    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    gap: 20px;
    padding: 7px;
    /* padding-left: 20px; */
    align-items: center;
}

.PlayerInfo :nth-child(1) {
    border-top-left-radius: 8px;
    /* border-left: none; */
}

.PlayerInfo :nth-child(8) {
    border-bottom-left-radius: 8px;
    /* border-left: none; */
}

.Age span {
    /* justify-content: space-a; */
    /* margin-right: 20px; */
    font-size: 1rem;

}

.Age {
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    padding-left: 20px;
}

.Age h2 {
    font-size: 1.8rem;
    color: rgb(187, 187, 187);
    text-align: center;
}

.TennisPlayer .Rank {
    grid-column: span 2;
}